<template>
  <div>
    <div class="title">
      更新行业资讯
    </div>
    <div class="WOBox">
        <el-button type="warning" @click="FnAdd()">新增</el-button>
        <el-divider></el-divider>
        <!-- 表格 -->
        <el-table
            :data="tableData"
            :show-header="false"
            style="width: 100%">
            <el-table-column
            label="历史发布">
                <template slot-scope="scope">
                    <div class="titnews">{{scope.row.k_title}}</div>
                    <div class="xxx">{{scope.row.k_content | rephtml}}</div>
                    <div class="tiemnews">{{scope.row.k_date | toTime}}</div>
                </template>
            </el-table-column>
            <el-table-column
            label="操作"
            width="180"
            align="right">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row.k_id)" type="text" size="small">查看</el-button>
                    <el-button @click="gotoeditknowledge(scope.row.k_id)" type="text" size="small">编辑</el-button>
                    <el-button type="text" @click="Fndeleteknowledge(scope.row.k_id)" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination
            background
            layout="prev, pager, next"
            :current-page="currentPage"
            @current-change="handleCurrentChange"
            :page-size="PageSize"
            :total="total">
        </el-pagination>
    </div>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from "vuex";
  export default {
    data() {
      return {
        tableData: [],//这是列表的内容
        total:0,//分页-总条目
        currentPage:1,//分页-当前页数
        PageSize:7,//每页有多少条数
      };
    },
    methods:{
        handleClick(k_id){//表格操作--查看点击
            this.$router.push({
                path:'/bkIndex/newsDetails', //也可以在路径上拼接
                query:{
                  k_id:k_id,      //传递的值
                  k_type:'行业资讯'
                }
            })
        },
        FnAdd(){//点击新增按钮
            // this.$router.push('/bkIndex/newsDet')
            this.$router.push({
                path:'/bkIndex/newsDet', //也可以在路径上拼接
                query:{
                k_id:-1  
                }
            })
        },
        knowledgelistv2(){//行业资讯列表
          url.knowledgelistv2({
            page:this.currentPage,
            per_page:this.PageSize,//每页显示条数
          })
          .then(res=>{
            console.log(res,'-----------行业资讯列表')
            if(res.data.code[0]='200'){
                let xx=res.data.data
                let newlist=[]
                xx.forEach(element => {
                    element.fields.k_id=element.pk
                    newlist.push(element.fields)
                });
                this.tableData=newlist
                this.total=res.data.datacount
            }
          })
        },
        handleCurrentChange(val) {//点击分页功能实现数据切换
          this.currentPage=val
          this.knowledgelistv2()//行业资讯列表
        },
        gotoeditknowledge(k_id){//修改行业资讯
          this.$router.push({
              path:'/bkIndex/newsDet', //也可以在路径上拼接
              query:{
                k_id:k_id, 
              }
          })
        },
        Fndeleteknowledge(k_id){//删除
            url.deleteknowledge({
                "user_id": this.buserInfo.user_id,
                "k_id": k_id,
            }).then(res=>{
                console.log(res,'--------------删除操作')
                if(res.data.code[0]='200'){
                    this.$message.success(res.data.message[0]);
                    this.knowledgelistv2()//调用知识库查询结果
                }
            })
        },
    },
    mounted(){
      this.knowledgelistv2()//行业资讯列表
    },
    computed:{
      ...mapState(['buserInfo'])
    }
  }
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.WOBox
    padding 30px
.xxx
  overflow:hidden; 
  text-overflow:ellipsis;
  display:-webkit-box; 
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2; 
.titnews
  font-size 17px
  font-weight bold
  line-height 170%
.tiemnews
  margin-left 6px
  font-size 14px
  color #999
</style>


